import React, { Component } from 'react';
import { withGoogleMap, withScriptjs, GoogleMap, Marker } from "react-google-maps";
import mapStyles from "./exportedVariables/ContactMapStyles";

function Map() {
    return (
        <GoogleMap
        defaultZoom={6}
        defaultCenter={{ lat: 46.432379, lng: 14.062334 }}
        defaultOptions={{ styles: mapStyles }}
        >

        <Marker position={{ lat: 46.432379, lng: 14.062334 }} />

        </GoogleMap>
    )
}

const MapWrapped = withScriptjs(withGoogleMap(Map));

export default class Contact extends Component {

    state = {
        email: "",
        subject: "",
        message: ""
    }
    
    change = e => {
        this.setState({ [e.target.name]: e.target.value })
    }
    
    onSubmit = e => {
        e.preventDefault()
    
        this.setState({
        email: "",
        subject: "",
        message: ""
        })
    }

    render() {
        return (
            <div className="contact">

                <div className="transition-animation">

                    <div className="open-top">
                        <div className="loader">
                            <div className="loader-bar"></div>
                        </div>
                        <div className="anim-box1"></div>
                    </div>

                    <div className="open-bottom">
                        <div className="anim-box2"></div>
                    </div>

                </div>

                <form className="contact-form">

                    <h1>Contact me</h1>

                    <input
                    name="email"
                    placeholder="Email"
                    maxLength="50"
                    value={this.state.email}
                    onChange={this.change}
                    />

                    <input
                    name="subject"
                    placeholder="Subject"
                    maxLength="50"
                    value={this.state.subject}
                    onChange={this.change}
                    />

                    <textarea
                    name="message"
                    placeholder="Message"
                    value={this.state.message}
                    onChange={this.change}
                    />

                    <button onClick={e => this.onSubmit(e)}> Send </button>
                    
                </form>

                <div className="google-map">
                <MapWrapped
                googleMapURL={`https://maps.googleapis.com/maps/api/js?v=3.exp&libraries=geometry,drawing,places&key=${process.env.REACT_APP_GOOGLE_KEY}`}
                loadingElement={<div style={{ height: `100%` }} />}
                containerElement={<div style={{ height: `100%` }} />}
                mapElement={<div style={{ height: `100%` }} />}
                />
                </div>

            </div>
        )
    }
}