import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import anime from 'animejs'; // https://animejs.com/documentation
import Particles from 'react-particles-js'; // https://www.npmjs.com/package/react-particles-js
import { particlesOptions } from './exportedVariables/HomeParticlesOptions';

export default class Home extends Component {

    firstname = React.createRef();
    lastname = React.createRef();
    logobox = React.createRef();
    logobox2 = React.createRef();
    textdev = React.createRef();
    textfront = React.createRef();
    icon1 = React.createRef();
    icon2 = React.createRef();
    icon3 = React.createRef();
    icon4 = React.createRef();

    componentDidMount() {

            anime({
                targets: this.firstname.current,
                translateX: "52vw",
                duration: 2550,
                delay: 140,
                easing: "easeInOutBack"
            })

            anime({
                targets: this.lastname.current,
                translateX: "52vw",
                duration: 2550,
                delay: 0,
                easing: "easeInOutBack"
            })

            anime({
                targets: this.logobox.current,
                opacity: [0, 1],
                translateY: [0, "0.8vmax"],
                duration: 1250,
                delay: 3000,
                easing: "easeOutSine"
            })

            anime({
                targets: this.logobox2.current,
                opacity: [0, 1],
                translateX: [0, "0.8vmax"],
                duration: 900,
                delay: 3400,
                easing: "easeOutSine"
            })

            anime({
                targets: this.textdev.current,
                opacity: [0, 1],
                translateX: [0, "2.8vmax"],
                duration: 1250,
                delay: 4250,
                easing: "easeOutSine"
            })

            anime({
                targets: this.textfront.current,
                opacity: [0, 1],
                translateX: [0, "-2.8vmax"],
                duration: 1250,
                delay: 4250,
                easing: "easeOutSine"
            })

            anime({
                targets: this.icon1.current,
                translateY: "86.3vh",
                duration: 3000,
                delay: 0,
                easing: 'easeInOutBack'
            })

            anime({
                targets: this.icon2.current,
                translateY: "86.3vh",
                duration: 3000,
                delay: 250,
                easing: 'easeInOutBack'
            })

            anime({
                targets: this.icon3.current,
                translateY: "86.3vh",
                duration: 3000,
                delay: 500,
                easing: 'easeInOutBack'
            })
            
            anime({
                targets: this.icon4.current,
                translateY: "86.3vh",
                duration: 3000,
                delay: 750,
                easing: 'easeInOutBack'
            })
    };

    render() {
        return (
            <div className="home">

                <div className="background-svg">
                </div>

                <div ref={this.logobox} className="logo-box">
                    <p ref={this.textdev}>Developer</p>
                </div>
                
                <div ref={this.logobox2} className="logo-box2">
                    <p ref={this.textfront}>Frontend</p>
                    <i className="code-icon material-icons">code</i>
                </div>

                <div className="box1">
                </div>
                    
                <div className="box2">
                </div>
                    
                <div className="box3"> 
                </div>
                    
                <div className="box4">
                </div>

                <Particles className="particles-js" params={particlesOptions} />

                <div className="particles-js-transparent-cover"></div>

                <div className="names">

                    <div ref={this.firstname} className="first-name">
                        <p>EDDY</p>
                    </div>

                    <div ref={this.lastname} className="last-name">
                        <p>HAMZICH.</p>
                    </div>

                </div>

                <NavLink to="/skills" className="call-to-action">
                    About me &nbsp;<i className="fas fa-angle-down"></i>
                </NavLink>

                <div className="icons">
                    <i ref={this.icon1} className="html fab fa-html5"></i>
                    <i ref={this.icon2} className="css fab fa-css3-alt"></i>
                    <i ref={this.icon3} className="javascript fab fa-js-square"></i>
                    <i ref={this.icon4} className="react fab fa-react"></i>
                </div>

            </div>
        )
    }
}